@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:200,300,400,600,700,900);
@import url(https://fonts.googleapis.com/css?family=Libre+Baskerville:400i);
@import url(https://fonts.googleapis.com/css?family=Roboto:100,300,400,500,700,900);

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.container-fulid{
  font-family: 'Source Sans Pro', sans-serif;
}

.Dashboard, .newSubscribe, .giving{
  background: -webkit-gradient(linear,left top, left bottom,color-stop(0, #feb37d),color-stop(0, #feb37d),color-stop(51%, #ff8867),to(#feb37d));
  background: -webkit-linear-gradient(top,#feb37d 0,#feb37d 0,#ff8867 51%,#feb37d);
  background: -o-linear-gradient(top,#feb37d 0,#feb37d 0,#ff8867 51%,#feb37d);
  background: linear-gradient(180deg,#feb37d 0,#feb37d 0,#ff8867 51%,#feb37d);
  height: 100vh;
  overflow-x: hidden;
}

.back{
  position: absolute;
  top: 8px;
}
.back button{
  color: #fff;
  text-transform: uppercase;
  font-size: 12px;
  letter-spacing: 2px;  
  background: transparent;
  border: none;
  border-bottom: 1px solid #fff;
  padding: 0;
  cursor: pointer;
}
.back i{
  font-size: 10px;
}
.btn-group .dropdown-comment{
  -webkit-touch-callout: none; 
  -webkit-user-select: none; 
  -moz-user-select: none; 
  -ms-user-select: none; 
  user-select: none;
  padding: .5rem 1rem;
  margin-left: 0;
  background: #cfcfcf;
  font-weight: 400;
  text-transform: none;
  width: 174px;  
  border-right: 1px solid #fff;
  font-size: 14px;
  color: #fff;
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;  
  margin-bottom: 0;
}

.btn-group .dropdown-actionbtn{
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
  cursor: pointer;
  background: #cfcfcf;
  padding: 0;
}
.btn-group .dropdown-actionbtn:focus{
  outline:0;
}

.btn-group .dropdown-actionbtn::after{
  content: "\E900";
  border: 0;
  width: 40px;
  padding-right: 8px;
  font-size: 22px;
  color: #fff;
  vertical-align: unset;
}
.btn-group .dropdown-menu{
  width: 100%;
}

.btn-group .dropdown-menu .dropdown-item{
  -webkit-touch-callout: none; 
  -webkit-user-select: none; 
  -moz-user-select: none; 
  -ms-user-select: none; 
  user-select: none;
  font-size: 14px;
}
.allcard{
  padding: 46px 52px;
  position: relative;
}
.card{
  border-radius: 30px;
  border: 0;
  -webkit-box-shadow: 1px 1px 8px 0 #858585;
          box-shadow: 1px 1px 8px 0 #858585;
}
.card-header{
  height: 60px;
  background: #ededed;
  border-top-left-radius: 30px !important;
  border-top-right-radius: 30px !important;
  padding: 0;
  position: relative;
}
.card-header p{
  display: inline-block;
  margin-bottom: 0;
  text-transform: uppercase;
  font-weight: 600;
  margin-left: 8px;
  letter-spacing: 1px;
}
.btn-red{
  position: absolute;
  font-size: 12px;
  text-transform: uppercase;
  padding: 10px 26px;
  border: 0;
  background: -webkit-gradient(linear,left top, left bottom,color-stop(0, #feaf78),color-stop(0, #fe8563),color-stop(0, #fc8362),color-stop(0, #feaf78),to(#f57754));
  background: -webkit-linear-gradient(top,#feaf78 0,#fe8563 0,#fc8362 0,#feaf78 0,#f57754);
  background: -o-linear-gradient(top,#feaf78 0,#fe8563 0,#fc8362 0,#feaf78 0,#f57754);
  background: linear-gradient(180deg,#feaf78 0,#fe8563 0,#fc8362 0,#feaf78 0,#f57754);
  border-radius: 30px;
  color: #fff;
  letter-spacing: 1px;
  font-weight: bold;
  cursor: pointer;
}
.btn-red:focus{
  -webkit-box-shadow: inset 0 3px 5px rgba(0,0,0,.125);
          box-shadow: inset 0 3px 5px rgba(0,0,0,.125);
}

.userImg{
  width: 38px;
}
.userStar{
  position: absolute;
  left: 38px;
  top: 34px;
}
.username{
  padding-left: 22px;
  font-weight: bold;
  text-decoration: underline;
  font-size: 18px;
}
tbody tr td:first-child{
  position: relative;
}
thead tr th{
  text-transform: uppercase;
  text-align: center;
  height: 64px;
  line-height: 38px;
  padding: 0;
}


