.giving-history .card-header .title{
  padding: 16.5px;
}
.giving-history .g-title-buttons .g-edit-gift{
  right: 210px;
  top: 12px;
}
.giving-history .g-title-buttons .g-export-excel{
  right: 20px;
  top: 12px;
}
.giving-history .g-selectDate, .giving-history .g-from, .giving-history .g-to{
  display: -ms-flexbox;
  display: flex;
}
.giving-history .g-selectDate{
  margin-bottom: 34px;
}
.g-selectDate .g-to{
  padding-left: 18px;
}
.g-selectDate .react-date-picker .react-date-picker__button{
  width: 184px;
  height: 36px;
  border: none;
  border-radius: 30px;
}
.g-selectDate .react-date-picker .react-date-picker__button, .g-selectDate .react-date-picker .react-date-picker__button input{
  background: #dedede;
  color: #fff;
}
.g-selectDate .react-date-picker .react-date-picker__button__input__month{
  border-top-left-radius: 30px;
  border-bottom-left-radius: 30px;
  margin-left: 16px;
}
.g-selectDate .react-date-picker .react-date-picker__button__input__year{
  width: 40px !important;
}
.g-selectDate .react-date-picker .react-date-picker__button .react-date-picker__calendar-button{
  padding-right: 12px;
  padding-left: 10px;
  background: #c4c4c4;
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
}
.g-selectDate p{
  margin-bottom: 0;
  line-height: 33px;
  padding-right: 8px;
  font-size: 14px;
  font-weight: bold;
}
.g-main-table .g-total-amount, .g-main-table .g-action{
  padding-top: 18px;
  font-size: 18px;
  font-weight: bold;
  text-align: center;
}
.g-main-table .g-action a{
  text-decoration: underline;
  color: #47cdd9;
}

@media screen and (max-width: 680px){
  .giving-history .card-header{
    height: auto;
  }
  .g-title-buttons{
    padding: 0 16.5px;
    margin-bottom: 10px;
  }
  .g-title-buttons button{
    position: inherit;
    width: 100%;
  }
  .giving-history .g-title-buttons .g-edit-gift{
    margin-bottom: 10px;
  }
}
@media screen and (max-width: 480px){
  .giving .allcard{
    padding: 46px 10px;
  }
}


